ransition: all 300ms ease;\n          -moz-transition: all 300ms ease;\n          -ms-transition: all 300ms ease;\n          -o-transition: all 300ms ease;\n          transition: all 300ms ease;\n          img {\n            margin-top: 38px;\n            display: inline-block;\n            max-width: 90%;\n            max-height: 70px;\n          }\n        }\n      }\n    }\n\n  }\n\n  @media @desktop {\n\n    .more_bonus {\n      .more_bonus_main {\n        .spm_col {\n          width: 50%;\n        }\n      }\n    }\n\n  }\n\n  @media @tablet {\n\n    .more_bonus {\n      .more_bonus_main {\n        .spm_col {\n          width: 50%;\n        }\n      }\n    }\n\n  }\n\n  @media @mobile {\n\n    .more_bonus {\n      .more_bonus_main {\n        .spm_col {\n          .mb_item {\n            height: auto;\n          }\n          width: 100%;\n          position: relative;\n          margin-right: 0px;\n          &.act {\n            margin-bottom: 0px;\n            height: 250px;\n          }\n          .mb_item_left {\n            float: right;\n            margin-top: 100px;\n            height: 140px;\n            width: 100%;\n            span {\n              width: 90%;\n              text-align: center;\n              margin-left: 5%;\n            }\n          }\n          .mb_item_right {\n            float: left;\n            width: 100%;\n            position: absolute;\n            top: 0px;\n            left: 0px;\n            height: 100px;\n            img {\n              margin-top: 23px;\n            }\n          }\n        }\n      }\n    }\n\n  }\n\n  //modal\n  .actions_selected_modal {\n\n    .bns_overlay_iner {\n      .box-sizing(border-box);\n      width: 600px;\n    }\n\n    .action_image {\n      display: inline-block;\n      .box-sizing(border-box);\n      width: 30%;\n      padding: 0;\n      vertical-align: middle;\n      overflow: hidden;\n      max-height: 170px;\n      img {\n        width: 100%;\n      }\n    }\n\n    .action_tools {\n\n      display: inline-block;\n      .box-sizing(border-box);\n      width: 69%;\n      padding: 0 0 0 40px;\n      vertical-align: middle;\n\n      .action_name {\n\n      }\n\n      .action_points {\n\n      }\n\n      .action_description {\n\n      }\n\n    }\n\n    [data-sailplay-action] {\n      position: relative;\n      width: 100%;\n      height: 36px;\n      display: inline-block;\n    }\n\n    .sailplay_action_frame {\n      position: absolute !important;\n      top: 0 !important;\n      left: 0 !important;\n      width: 140px !important;\n      height: 100% !important;\n      overflow: visible !important;\n      border: none !important;\n    }\n\n    .action_buttons {\n      margin-top: 30px;\n    }\n\n  }\n\n  .actions_custom_selected_modal {\n\n    .sailplay_action_custom_frame {\n      width: 100%;\n      min-height: 400px;\n    }\n\n    .bns_overlay_iner {\n      .box-sizing(border-box);\n      width: 600px;\n    }\n\n  }\n\n  .quiz_selected_modal {\n\n    .bns_overlay_iner {\n      .box-sizing(border-box);\n      width: 600px;\n    }\n\n  }\n\n  .quiz_block {\n    width: 100%;\n    border-radius: 10px;\n    box-sizing: border-box;\n\n    .button_wrapper {\n      margin: 5px 0;\n    }\n\n    &__title {\n      width: 100%;\n      color: #57baad;\n      font-size: 18px;\n      font-weight: bold;\n      text-align: center;\n    }\n\n    label {\n      display: block;\n      width: 100%;\n      font-size: 14px;\n      margin: 10px 0;\n      white-space: nowrap;\n\n      input {\n        display: inline-block;\n        height: 14px;\n        width: 14px;\n        line-height: 14px;\n        vertical-align: middle;\n      }\n\n      span {\n        white-space: normal;\n        display: inline-block;\n        vertical-align: middle;\n        line-height: 18px;\n      }\n\n    }\n\n    &__counter {\n      width: 100%;\n      display: block;\n      margin: 5px 0;\n      text-align: center;\n      font-weight: bold;\n      font-size: 18px;\n      color: #57b